<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>&nbsp;&nbsp;<i class="el-icon-lx-lock"></i>&nbsp;&nbsp;&nbsp;招聘信息
        </el-breadcrumb-item>
        <el-breadcrumb-item>详情</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="form-box">
        <el-form ref="form" :model="form" label-width="160px">
          <el-form-item label="职位名称" prop="name" :label-width="formLabelWidth">
            <el-input v-model="form.name" disabled="" autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item label="招聘人数" prop="people" :label-width="formLabelWidth">
            <el-input v-model="form.people" disabled="" autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item label="薪资" prop="salary" :label-width="formLabelWidth">
            <el-input v-model="form.salary" disabled="" autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item label="地址" prop="address" :label-width="formLabelWidth">
            <el-input v-model="form.address" disabled="" autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
<!--          <el-form-item label="截止时间" prop="endtime" :label-width="formLabelWidth">-->
<!--            <el-input v-model="form.endtime" disabled="" autocomplete="off" class="mz_wb"></el-input>-->
<!--          </el-form-item>-->
          <el-form-item label="工作要求" prop="wordrequire" :label-width="formLabelWidth">
            <el-input type="textarea" v-model="form.wordrequire" disabled="" autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item label="工作内容" prop="wordinfo" :label-width="formLabelWidth">
            <el-input type="textarea" disabled="" v-model="form.wordinfo" autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item class="submit">
<!--            <el-button type="primary" @click="onSubmit('form')">提交</el-button>-->
            <el-button @click="$router.go(-1)">返回</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
  import http from '../../../assets/js/http'

  export default {
    name: 'recruitD',
    data: function () {
      return {
        form: {},
        formLabelWidth: '100px'
      }
    },
    created() {

    },
    activated(){
      this.getV()
    },
    methods: {
      getV() {
        var that = this;
        var params = {
          'datoken': that.$route.query.datoken,
        };
        that.apiPost('/admin/djzpone', params).then((res) => {
          this.handelResponse(res, (data) => {
            that.form = data.data;
          })
        })
      },
    },
    mixins: [http]
  }
</script>
<style>
  .el-upload--text {
    width: 180px !important;
  }
  .el-upload--text img {
    width: 100%;
    height: 100%;
  }
  textarea{
    height: 100px;
  }
</style>



